.App {
  text-align: center;
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-height: 10vh;
  background: rgb(73, 79, 82);
  color: white;
}

.nav-links {
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
.img-logo {
  height: 7vh;
  padding: 5px;
}
.img-portrait {
  height: 40vh;
  width: 40vh;
  border-radius: 16px;
}

.nav_icons {
  display: flex;
  justify-content: space-between;
  max-width: 400px;
  margin: 2 auto;
  padding: 10px 0;
}
.nav_icons_left {
  display: flex;
  max-width: 400px;
  margin: 2 auto;
  padding: 10px 0;
  align-content: left;
  position: left;
  padding-left: 0;
  padding-right: 55%;
}
.home-a {
  color: #2867b2;
}
